@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap);
body,h1{margin:0;font-family:"Roboto", sans-serif;font-weight:500}h1{padding:2vh}.App{background-color:rgba(74,221,160,0.57);text-align:center}

.todo-list{width:80vw;margin:0 auto;padding:0}.todo-list__item{display:flex;height:10vh;align-items:center;justify-content:space-between;text-align:center;margin-bottom:1vh;padding:2vh;background-color:#fff;border-radius:1em}.todo-list__item span{display:inline-block;text-align:left;width:23%}.todo-list__item-status{width:2em;height:2em;border:gray 0.1em solid;border-radius:10%}.todo-list__item-status--completed{background-image:url(../../static/media/vcsnormal_93488.a3cb7f14.svg);background-size:cover}

